.pl__left,
.pl__right {
  position: absolute;
  background: #111111;
  width: 51%;
  height: 100%;
  z-index: 2001;
}

.pl__left {
  left: 0;
}

.pl__right {
  right: 0;
}

.loaded .pl__left {
  transform: translate(-100%);
  transition: transform .3s .4s;
}

.loaded .pl__right {
  transform: translate(100%);
  transition: transform .3s .4s;
}

.round,
.round:before,
.round:after {
  border: 3px solid transparent;
  border-radius: 50%;
}

.loaded .round {
  display: none;
}

.round {
  width: 9.375rem;
  height: 9.375rem;
  top: 50vh;
  left: 50%;
  margin: -4.6875rem 0 0 -4.6875rem;
  position: absolute;
  border-top-color: #d1c905;
  animation: spin 1s infinite linear;
  z-index: 2002;
}

.round:before {
  content: '';
  position: absolute;
  top: 0.625rem;
  left: 0.625rem;
  right: 0.625rem;
  bottom: 0.625rem;
  border-top-color: #00abd4;
  animation: spin 2s infinite linear;
}

.round:after {
  content: '';
  position: absolute;
  top: 2.6875rem;
  left: 2.6875rem;
  right: 2.6875rem;
  bottom: 2.6875rem;
  border-top-color: #ff1400;
  animation: antispin .3s infinite linear;
  border-bottom-color: #ff1400;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(-360deg);
  }
}

