.steps__items:first-child {
  margin-left: 0;
}

.steps__items:last-child {
  margin-right: 0;
}

.steps__items:hover {
  border: 0.1875rem solid #777;
  background: #fdffc4;
  box-shadow: 0 0 0 4px #aaa;
}

.steps__items-sprite {
  display: inline-block;
  margin-top: 1.875rem;
  width: 2.8125rem;
