@media screen and (max-width: 1200px) {
  html {
    font-size: 16px;
  }

  .container {
    max-width: 1000px;
  }
}

@media screen and (max-width: 992px) {
  html {
    font-size: 15px;
  }

  .container {
    max-width: 750px;
    padding: 0 2.5rem;
  }

  .header__nav {
    display: none;
  }

  .hamburger {
    display: inline-block;
    position: fixed;
    z-index: 2;
    right: 5%;
    padding: 20px 5px 15px 15px;
    display: inline-block;
    cursor: pointer;
    transition-property: opacity, filter;
    transition-duration: 0.15s;
    transition-timing-function: linear;
    font: inherit;
    color: inherit;
    text-transform: none;
    background-color: transparent;
    border: 0;
    margin: 0;
    overflow: visible;
    outline: none;
    transition: .3s;
  }

  .hamburger:hover-inner::before {
    transform: translate3d(8px, 0, 0) rotate(45deg) scale(0.7, 1);
  }

  .hamburger:hover-inner::after {
    transform: translate3d(8px, 0, 0) rotate(-45deg) scale(0.7, 1);
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 14px;
  }

  .container {
    max-width: 480px;
    padding: 0 2.5rem;
  }

  .presentation__title-top {
    font-size: 2rem;
  }

  .img__wrap_less {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .container {
    width: auto;
    padding: 0 2.5rem;
  }

  .hamburger {
    right: 2%;
  }

  .ps__items-title,
  .ps__middle-title {
    font-size: 1.25rem;
    color: #000;
  }
}